import React, { useState } from 'react';
import { Typography, TextField, Button, Box } from '@mui/material';

const OwnerInfo = ({ info, setInfo }) => {
  const [isEditing, setIsEditing] = useState(false);
  const [editedInfo, setEditedInfo] = useState(info);

  const handleChange = (e) => {
    setEditedInfo({ ...editedInfo, [e.target.name]: e.target.value });
  };

  const handleSave = () => {
    setInfo(editedInfo);
    setIsEditing(false);
  };

  const handleCancel = () => {
    setEditedInfo(info);
    setIsEditing(false);
  };

  return (
    <Box>
      <Typography variant="h6" gutterBottom component="div">
        
      </Typography>
      {isEditing ? (
        <>
          <TextField
            fullWidth
            label=""
            name="name"
            value={editedInfo.name}
            onChange={handleChange}
            margin="normal"
          />
          <TextField
            fullWidth
            label=""
            name="email"
            value={editedInfo.email}
            onChange={handleChange}
            margin="normal"
          />
          <TextField
            fullWidth
            label=""
            name="phone"
            value={editedInfo.phone}
            onChange={handleChange}
            margin="normal"
          />
          <Box mt={2}>
            <Button onClick={handleSave} variant="contained" color="primary" sx={{ mr: 1 }}>
              
            </Button>
            <Button onClick={handleCancel} variant="outlined">
              
            </Button>
          </Box>
        </>
      ) : (
        <>
          <Typography><strong>:</strong> {info.name}</Typography>
          <Typography><strong>:</strong> {info.email}</Typography>
          <Typography><strong>:</strong> {info.phone}</Typography>
          <Button onClick={() => setIsEditing(true)} variant="outlined" color="primary" sx={{ mt: 2 }}>
            
          </Button>
        </>
      )}
    </Box>
  );
};

export default OwnerInfo;