import React, { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import { 
  Container, Typography, Table, TableBody, TableCell, TableContainer, TableHead, TableRow, 
  Paper, TextField, Checkbox, Button, Grid, AppBar, Toolbar, IconButton, FormControlLabel, Dialog, DialogTitle, DialogContent, DialogActions
} from '@mui/material';
import ArrowBackIcon from '@mui/icons-material/ArrowBack';

const BulkMenuEdit = () => {
  const navigate = useNavigate();
  const [menus, setMenus] = useState([]);
  const [editingMenu, setEditingMenu] = useState(null);
  const [openDialog, setOpenDialog] = useState(false);

  useEffect(() => {
    // 20 ()
    const initialMenus = [
      // ... 20
      { id: 1, name: '', price: 800, category: '', stores: { 1: true, 2: true, 3: false }, priorityStores: { 1: false, 2: false, 3: false }, calories: 450, protein: 20, fat: 25, carbs: 30, description: '', takeout: true },
      { id: 2, name: '', price: 700, category: '', stores: { 1: true, 2: true, 3: true }, priorityStores: { 1: true, 2: false, 3: false }, calories: 400, protein: 15, fat: 20, carbs: 25, description: '', takeout: true },
      { id: 3, name: '', price: 750, category: '', stores: { 1: true, 2: false, 3: true }, priorityStores: { 1: false, 2: false, 3: true }, calories: 380, protein: 18, fat: 15, carbs: 35, description: '', takeout: true },
      { id: 4, name: '', price: 500, category: '', stores: { 1: true, 2: true, 3: true }, priorityStores: { 1: false, 2: true, 3: false }, calories: 300, protein: 12, fat: 15, carbs: 30, description: '', takeout: true },
      { id: 5, name: '', price: 900, category: '', stores: { 1: true, 2: false, 3: true }, priorityStores: { 1: true, 2: false, 3: false }, calories: 550, protein: 22, fat: 30, carbs: 60, description: '', takeout: false },
      { id: 6, name: '', price: 850, category: '', stores: { 1: true, 2: true, 3: true }, priorityStores: { 1: false, 2: false, 3: true }, calories: 600, protein: 20, fat: 18, carbs: 90, description: '', takeout: true },
      { id: 7, name: '', price: 950, category: '', stores: { 1: true, 2: true, 3: false }, priorityStores: { 1: false, 2: true, 3: false }, calories: 500, protein: 25, fat: 20, carbs: 50, description: '', takeout: true },
      { id: 8, name: '', price: 880, category: '', stores: { 1: true, 2: false, 3: true }, priorityStores: { 1: true, 2: false, 3: false }, calories: 350, protein: 18, fat: 15, carbs: 40, description: '8', takeout: true },
      { id: 9, name: '', price: 1000, category: '', stores: { 1: true, 2: true, 3: true }, priorityStores: { 1: false, 2: false, 3: true }, calories: 400, protein: 20, fat: 25, carbs: 20, description: '', takeout: true },
      { id: 10, name: '', price: 600, category: '', stores: { 1: true, 2: true, 3: true }, priorityStores: { 1: false, 2: true, 3: false }, calories: 250, protein: 8, fat: 12, carbs: 30, description: '', takeout: true },
      { id: 11, name: '', price: 700, category: '', stores: { 1: true, 2: false, 3: true }, priorityStores: { 1: true, 2: false, 3: false }, calories: 200, protein: 10, fat: 8, carbs: 20, description: '', takeout: false },
      { id: 12, name: '', price: 850, category: '', stores: { 1: true, 2: true, 3: false }, priorityStores: { 1: false, 2: false, 3: false }, calories: 450, protein: 18, fat: 12, carbs: 70, description: '', takeout: true },
      { id: 13, name: '', price: 400, category: '', stores: { 1: true, 2: true, 3: true }, priorityStores: { 1: false, 2: true, 3: false }, calories: 180, protein: 5, fat: 6, carbs: 30, description: '', takeout: true },
      { id: 14, name: '', price: 550, category: '', stores: { 1: true, 2: false, 3: true }, priorityStores: { 1: true, 2: false, 3: true }, calories: 320, protein: 14, fat: 18, carbs: 30, description: '', takeout: true },
      { id: 15, name: '', price: 1100, category: '', stores: { 1: true, 2: true, 3: false }, priorityStores: { 1: false, 2: true, 3: false }, calories: 650, protein: 30, fat: 50, carbs: 15, description: '', takeout: true },
      { id: 16, name: '', price: 600, category: '', stores: { 1: true, 2: true, 3: true }, priorityStores: { 1: false, 2: false, 3: true }, calories: 150, protein: 6, fat: 10, carbs: 15, description: '', takeout: true },
      { id: 17, name: '', price: 780, category: '', stores: { 1: true, 2: false, 3: true }, priorityStores: { 1: true, 2: false, 3: false }, calories: 300, protein: 25, fat: 15, carbs: 10, description: '', takeout: true },
      { id: 18, name: '', price: 750, category: '', stores: { 1: true, 2: true, 3: false }, priorityStores: { 1: false, 2: true, 3: false }, calories: 350, protein: 12, fat: 25, carbs: 25, description: '', takeout: true },
      { id: 19, name: '', price: 900, category: '', stores: { 1: true, 2: true, 3: true }, priorityStores: { 1: false, 2: false, 3: true }, calories: 550, protein: 30, fat: 35, carbs: 25, description: '', takeout: true },
      { id: 20, name: '', price: 850, category: '', stores: { 1: true, 2: false, 3: true }, priorityStores: { 1: true, 2: false, 3: false }, calories: 580, protein: 22, fat: 15, carbs: 85, description: '', takeout: true },
    ];
    setMenus(initialMenus);
  }, []);

  const handleEdit = (menu) => {
    setEditingMenu({...menu});
    setOpenDialog(true);
  };

  const handleMenuChange = (field, value) => {
    setEditingMenu({ ...editingMenu, [field]: value });
  };

  const handleSave = () => {
    setMenus(menus.map(menu => menu.id === editingMenu.id ? editingMenu : menu));
    setOpenDialog(false);
    setEditingMenu(null);
  };

  const handleStoreChange = (menuId, storeId, isChecked, isPriority) => {
    setMenus(menus.map(menu => {
      if (menu.id === menuId) {
        const updatedStores = { ...menu.stores, [storeId]: isChecked };
        const updatedPriorityStores = { ...menu.priorityStores, [storeId]: isPriority };
        return { ...menu, stores: updatedStores, priorityStores: updatedPriorityStores };
      }
      return menu;
    }));
  };

  return (
    <>
      <AppBar position="static">
        <Toolbar>
          <IconButton edge="start" color="inherit" onClick={() => navigate('/')} sx={{ mr: 2 }}>
            <ArrowBackIcon />
          </IconButton>
          <Typography variant="h6" component="div" sx={{ flexGrow: 1 }}>
            
          </Typography>
        </Toolbar>
      </AppBar>
      <Container maxWidth="lg" sx={{ mt: 4 }}>
        <TableContainer component={Paper}>
          <Table>
            <TableHead>
              <TableRow>
                <TableCell></TableCell>
                <TableCell></TableCell>
                <TableCell></TableCell>
                <TableCell></TableCell>
                <TableCell></TableCell>
                <TableCell></TableCell>
                <TableCell></TableCell>
              </TableRow>
            </TableHead>
            <TableBody>
              {menus.map((menu) => (
                <TableRow key={menu.id}>
                  <TableCell>{menu.name}</TableCell>
                  <TableCell>{menu.price}</TableCell>
                  <TableCell>{menu.category}</TableCell>
                  {[1, 2, 3].map((storeId) => (
                    <TableCell key={storeId}>
                      <Checkbox
                        checked={menu.stores[storeId]}
                        onChange={(e) => handleStoreChange(menu.id, storeId, e.target.checked, menu.priorityStores[storeId])}
                      />
                      <Checkbox
                        checked={menu.priorityStores[storeId]}
                        onChange={(e) => handleStoreChange(menu.id, storeId, menu.stores[storeId], e.target.checked)}
                      />
                    </TableCell>
                  ))}
                  <TableCell>
                    <Button variant="contained" color="primary" onClick={() => handleEdit(menu)}>
                      
                    </Button>
                  </TableCell>
                </TableRow>
              ))}
            </TableBody>
          </Table>
        </TableContainer>

        <Dialog open={openDialog} onClose={() => setOpenDialog(false)}>
          <DialogTitle></DialogTitle>
          <DialogContent>
            {editingMenu && (
              <Grid container spacing={2}>
                <Grid item xs={6}>
                  <TextField fullWidth label="" value={editingMenu.name} onChange={(e) => handleMenuChange('name', e.target.value)} />
                </Grid>
                <Grid item xs={6}>
                  <TextField fullWidth label="" type="number" value={editingMenu.price} onChange={(e) => handleMenuChange('price', Number(e.target.value))} />
                </Grid>
                <Grid item xs={6}>
                  <TextField fullWidth label="" value={editingMenu.category} onChange={(e) => handleMenuChange('category', e.target.value)} />
                </Grid>
                <Grid item xs={6}>
                  <TextField fullWidth label="" type="number" value={editingMenu.calories} onChange={(e) => handleMenuChange('calories', Number(e.target.value))} />
                </Grid>
                <Grid item xs={4}>
                  <TextField fullWidth label="" type="number" value={editingMenu.protein} onChange={(e) => handleMenuChange('protein', Number(e.target.value))} />
                </Grid>
                <Grid item xs={4}>
                  <TextField fullWidth label="" type="number" value={editingMenu.fat} onChange={(e) => handleMenuChange('fat', Number(e.target.value))} />
                </Grid>
                <Grid item xs={4}>
                  <TextField fullWidth label="" type="number" value={editingMenu.carbs} onChange={(e) => handleMenuChange('carbs', Number(e.target.value))} />
                </Grid>
                <Grid item xs={12}>
                  <TextField fullWidth multiline rows={4} label="" value={editingMenu.description} onChange={(e) => handleMenuChange('description', e.target.value)} />
                </Grid>
                <Grid item xs={12}>
                  <FormControlLabel
                    control={
                      <Checkbox 
                        checked={editingMenu.takeout} 
                        onChange={(e) => handleMenuChange('takeout', e.target.checked)}
                      />
                    }
                    label=""
                  />
                </Grid>
              </Grid>
            )}
          </DialogContent>
          <DialogActions>
            <Button onClick={() => setOpenDialog(false)}></Button>
            <Button onClick={handleSave} variant="contained" color="primary"></Button>
          </DialogActions>
        </Dialog>
      </Container>
    </>
  );
};

export default BulkMenuEdit;