import React from 'react';
import { Typography, Grid } from '@mui/material';

const ContractInfo = ({ info = {} }) => {
  const { 
    totalStores = 0, 
    basicFee = 0, 
    optionContracts = 0, 
    optionFee = 0, 
    totalMenus = 0 
  } = info;

  return (
    <div>
      <Typography variant="h5" gutterBottom></Typography>
      <Grid container spacing={2}>
        <Grid item xs={6}>
          <Typography><strong>:</strong> {totalStores}</Typography>
          <Typography><strong>:</strong> {basicFee.toLocaleString()}</Typography>
          <Typography><strong>:</strong> {optionContracts}</Typography>
        </Grid>
        <Grid item xs={6}>
          <Typography><strong>:</strong> {optionFee.toLocaleString()}</Typography>
          <Typography><strong>:</strong> {totalMenus}</Typography>
          <Typography><strong>:</strong> {(basicFee + optionFee).toLocaleString()}</Typography>
        </Grid>
      </Grid>
    </div>
  );
};

export default ContractInfo;